@import url("https://fonts.googleapis.com/css2?family=Recursive&display=swap");

.main {
  border-radius: 20px 20px 20px 20px;
  background-color: #01579b;

  height: 350px;
  width: 600px;
  margin: auto;
  margin-top: 200px;
  margin-bottom: 500px;
}

.top {
  height: 40px;
  background-color: #812362;
  color: whitesmoke;
  padding: 10px;
  border-radius: 20px 20px 0 0;
  font-family: "Recursive", sans-serif;
  display: flex;
  justify-content: space-between;
}

.header {
  background-color: #424242;
  color: whitesmoke;
  margin: 0px 0px 0px 0px;
  font-size: 50px;
  border-radius: 20px 20px 0 0;
  font-family: "Recursive", sans-serif;
}

.button {
  width: 35px;
  height: 35px;
}

.day {
  padding: 15px;
  color: whitesmoke;
  font-family: "Recursive", sans-serif;
  font-size: 18px;
  font-weight: 600;
}

.temp {
  padding: 15px;
  color: whitesmoke;
  font-family: "Recursive", sans-serif;
  font-size: 18px;
}

.flex {
  display: flex;
  justify-content: space-between;
}

.sunrise-sunset {
  padding: 15px;
  color: whitesmoke;
  font-family: "Recursive", sans-serif;
  font-size: 16px;
}

.description {
  padding: 15px;
  color: whitesmoke;
  font-family: "Recursive", sans-serif;
  font-size: 24px;
  font-weight: 600;
}
